export default {
  name: "TokenEdit",
  beforeCreate: function beforeCreate() {
    this.$bind.mapGetters('TokenResource', {
      destroying: 'destroying',
      abilities: 'abilities',
      entity: 'entity'
    });
    this.$bind.mapActions('TokenResource', {
      update: 'update',
      create: 'create',
      store: 'store'
    });
    this.$bind.mapState('TokenResource', {
      $errors: '$errors',
      grants: 'grants'
    });
  },
  created: function created() {
    //this.$errors.clear()
    if (!this.entity.abilities.length) {
      this.entity.abilities.push({});
    }
  },
  beforeDestroy: function beforeDestroy() {//this.$errors.clear()
  }
};